<template>
    <div>
      <div>list</div>
      <div v-html="html"></div>
    </div>
</template>

<script>
    import marked from 'marked'
    export default {

        async created() {
          let {data}=await this.$axios.get('/static/test.md')
          this.html=marked(data)
        },

        data(){
          return{
            html:""
          }
        }
    }
</script>

<style scoped>

</style>



